<template>
  <div id="login">
    <div class="container">
      <h3>Please Login</h3>
      <div class='login card-panel center'>
        <form class="col s12 m8 offset-m2" method="post">
          <div class='row'>

            <div class='input-field col s12'>
              <i class="material-icons prefix">email</i>
              <input class='validate' type='email' name='email' id='email' v-model="email"/>
              <label for='email'>Enter your email</label>
            </div>

            <div class='input-field col s12'>
              <i class="material-icons prefix">lock</i>
              <input class='validate' type='password' name='password' id='password' v-model="password" />
              <label for='password'>Enter your password</label>
            </div>

            <label style='float: right;'>
              <a class='pink-text' href='#!'><b>Forgot Password?</b></a>
            </label>

            <br />
            <center>
              <button v-on:click="login" type='submit' name='btn_login' class='col s12 btn btn-large waves-effect blue'>Login</button>
            </center>
          </div>
        </form>
      </div>
      <a href="#!">Create account</a>
    </div>
  </div>
</template>

<style>
body {
    display: flex;
    min-height: 100vh;
    flex-direction: column;
}

main {
    flex: 1 0 auto;
}

body {
    background: #fff;
}

.input-field input[type=date]:focus + label,
.input-field input[type=text]:focus + label,
.input-field input[type=email]:focus + label,
.input-field input[type=password]:focus + label {
  color: #e91e63;
}

.input-field input[type=date]:focus,
.input-field input[type=text]:focus,
.input-field input[type=email]:focus,
.input-field input[type=password]:focus {
  border-bottom: 2px solid #e91e63;
  box-shadow: none;
}
</style>

<script>
import firebase from 'firebase'
export default {
  name: 'login',
  data: function () {
    return {
      email: '',
      password: ''
    }
  },
  methods: {
    login: function (e) {
      firebase.auth().signInUserWithEmailAndPassword(this.email, this.password)
        .then(user => {
          alert(`You are logged in as ${user.email}`)
          this.$router.push('/')
        }, err => {
          alert(err.message)
        })
      e.preventDefault()
    }
  }
}
</script>



