<template>
  <div id="new-employee">
    <h3>New Employee</h3>
    <div class="row">
      <form @submit="saveEmployee" class="col s12">
        <div class="row">
          <div class="input-field col-s12">
            <input type="text" v-model="employee_id" reuire />
            <label>Employee ID</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field col-s12">
            <input type="text" v-model="name" reuire />
            <label>Name</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field col-s12">
            <input type="text" v-model="department" reuire />
            <label>Department</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field col-s12">
            <input type="text" v-model="position" reuire />
            <label>Position</label>
          </div>
        </div>
        <button type="submit" class="btn">Submit</button>
        <router-link to="/" class="btn grey">Cancel</router-link>
      </form>
    </div>
  </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'new-employee',
  data () {
    return {
      employee_id: null,
      name: null,
      department: null,
      position: null
    }
  },
  methods: {
    saveEmployee () {
      db.collection('employees').add({
        employee_id: this.employee_id,
        name: this.name,
        department: this.department,
        position: this.position
      })
        .then(docRef => {
          this.$router.push('/')
        })
        .catch(error => console.log(error))
    }
  }
}
</script>



